.add {
  &__title {
    font-weight: 600;
    font-size: 60px;
    line-height: 70px;
    color: #fff;
    text-align: left;
    margin-bottom: 50px;

    @media (max-width: 768px) {
      font-size: 40px;
      line-height: 50px;
    }

    @media (max-width: 600px) {
      font-size: 24px;
      line-height: 26px;
    }
  }

  &__form {
    display: flex;
    justify-content: space-between;

    .add__left {
      background-color: #fff;
    }

    @media (max-width: 1200px) {
      flex-direction: column;
    }
  }

  &__left {
    box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.05), 1px 1px 15px rgba(0, 0, 0, 0.05);
    border-radius: 25px;
    border: 1px solid #fff;
    width: 50%;
    padding: 50px 25px;

    @media (max-width: 1200px) {
      width: 100%;
      margin-bottom: 40px;
    }

    @media (max-width: 600px) {
      padding: 10px;
    }
  }

  &__form-title {
    font-weight: 500;
    font-size: 32px;
    line-height: 42px;
    color: $color-green;
    margin-bottom: 50px;

    @media (max-width: 600px) {
      font-size: 22px;
      line-height: 24px;
      margin-bottom: 30px;
    }
  }

  &__form-label {
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    letter-spacing: 0.03em;
    text-transform: uppercase;

    @media (max-width: 768px) {
      flex-direction: column;
      margin-bottom: 10px;
    }

    @media (max-width: 600px) {
      font-size: 16px;
      line-height: 18px;
    }
  }

  &__form-input {
    border: none;
    width: 369px;
    height: 60px;
    left: 558px;
    top: 462px;
    background: #E8E8E8;
    border-radius: 25px;
    padding: 10px 25px;

    @media (max-width: 768px) {
      margin-top: 10px;
    }

    @media (max-width: 600px) {
      width: 100%;
    }
  }

  &__form-input--name {
    width: 450px;

    @media (max-width: 600px) {
      width: 100%;
    }
  }

  &__left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 50%;
    margin-right: 20px;

    @media (max-width: 1200px) {
      width: 100%;
      margin-right: 0;
    }
  }

  &__right-balance {
    width: 650px;
    height: 333px;
    background: #FFFFFF;
    box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.05), 1px 1px 15px rgba(0, 0, 0, 0.05);
    border-radius: 25px;
    position: relative;
    padding: 25px;

    @media (max-width: 1600px) {
      max-width: 100%;
      width: 100%;
    }

    @media (max-width: 1200px) {
      margin-bottom: 20px;
    }

    @media (max-width: 600px) {}

    &:after {
      content: "";
      position: absolute;
      right: 0;
      bottom: -105px;
      background-image: url(../img/addbalance-bg.svg);
      background-repeat: no-repeat;
      background-size: contain;
      width: 400px;
      height: 380px;

      @media (max-width: 600px) {
        display: none;
      }
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;

    @media (max-width: 1200px) {
      width: 100%;
    }
  }

  &__right-title {
    color: $color-green;
    font-weight: 600;
    font-size: 50px;
    line-height: 60px;
    margin-bottom: 25px;

    @media (max-width: 600px) {
      font-size: 30px;
      line-height: 40px;
    }
  }

  &__right-vol {
    color: $color-green;
    font-weight: 600;
    font-size: 50px;
    line-height: 60px;

    @media (max-width: 600px) {
      font-size: 30px;
      line-height: 40px;
    }
  }

  &__btn {
    width: 258px;
    height: 70px;
    font-weight: 600;
    font-size: 22px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    padding: 15px 20px;
    background-color: #fff;
    color: $color-green;
    margin-left: auto;
    border: 1px solid #fff;

    &:hover {
      background-color: $color-green;
      color: #fff;
    }

    @media (max-width: 600px) {
      width: 100%;
      height: 40px;
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__btns {
    margin-left: auto;
    text-align: right;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  &__button {
    margin-bottom: 25px;
    width: 400px;

    @media (max-width: 600px) {
      width: 100%;
      height: 40px;
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__inner {
    margin-bottom: 50px;
    background-color: $color-green;
  }
}