.burger {
  @include burger;
  display: none;
  margin-left: 10px;
  position: relative;
  z-index: 100;

  @media (max-width: 1200px) {
    display: block;
  }
}

.header-lk {
  .header__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 600px) {
      flex-wrap: wrap;
    }
  }

  .header__logo {
    display: flex;
    align-items: center;
    font-weight: 700;
    width: auto;
  }

  .header__logo-text {
    color: $color-green;
    margin-left: 5px;

    @media (max-width:1280px) {
      display: none;
    }

    span {
      color: $color-main;
    }
  }

  .nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: auto;
    margin-right: 10px;

    @media (max-width:1200px) {
      display: none;
    }
  }

  .nav__list {
    display: flex;
    align-items: center;

    @media (max-width:1650px) {
      font-size: 18px;
    }
  }

  .nav__item:not(:last-child) {
    margin-right: 40px;

    @media (max-width:1650px) {
      margin-right: 0;
    }
  }

  .nav__link {
    font-weight: 500;
    padding: 5px 10px;
    border-radius: 15px;

    @media (max-width:1650px) {
      font-size: 16px;
    }
  }

  .nav__link--active {
    background: #E0E0E0;
  }

  .header-lk__search {
    position: relative;
    margin-left: 50px;
    margin-bottom: 0;

    @media (max-width:1650px) {
      margin-left: 25px;
    }

    @media (max-width: 600px) {
      margin: 0 auto;
      margin-top: 10px;
      order: 5;
    }

    &:before {
      content: "";
      position: absolute;
      top: 5px;
      left: 10px;
      width: 50px;
      height: 50px;
      background-image: url(../img/search-icon.png);
      background-repeat: no-repeat;
      background-size: contain;

      @media (max-width:1650px) {
        width: 40px;
        height: 40px;
      }
    }
  }

  .header-lk__serch-input {
    min-width: 300px;
    width: 485px;
    height: 60px;
    background: #E8E8E8;
    border-radius: 25px;
    padding: 10px 10px 10px 80px;

    @media (max-width:1650px) {
      width: 250px;
      min-width: 250px;
      height: 50px;
    }

    &::placeholder {
      font-weight: 400;
      font-size: 22px;
      line-height: 32px;
      color: #95A5A6;

      @media (max-width:1650px) {
        font-size: 18px;
        line-height: 22px;
      }
    }
  }

  .header-lk__phone {
    display: block;
    width: 50px;
    height: 50px;
    background-image: url(../img/call-icon.png);
    background-size: contain;
    background-repeat: no-repeat;
    margin-left: 25px;
  }

  .header__login {
    margin-left: 25px;
  }

  .nav.menu--active {
    margin: 0;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    background-color: rgba($color-green, 0.9);
    z-index: 10;
    min-height: 100vh;
  }

  .menu--active {
    .nav__list {
      display: block;
      padding: 100px 10px 10px 10px;
      text-align: center;
    }

    .nav__item {
      margin-bottom: 20px;
    }
  }
}