.offer-lk {
  &__wrapper {
    display: flex;
    margin: 100px 0 200px;

    @media (max-width: 1200px) {
      margin: 50px 0 60px;
    }

    @media (max-width: 968px) {
      flex-direction: column;
    }
  }

  &__content {
    background: #FFFFFF;
    box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.05), 1px 1px 15px rgba(0, 0, 0, 0.05);
    border-radius: 25px;
    padding: 25px 25px 50px 25px;
    width: 100%;
    border: 1px solid #ccc;
  }

  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 42px;
    text-transform: none;
    color: $color-main;
    display: block;
    margin-bottom: 45px;
    padding-bottom: 15px;
    border-bottom: 2px solid $color-green;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 25px;
    row-gap: 50px;

    @media (max-width: 1500px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media (max-width: 1200px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 968px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item-image {
    height: 300px;
    width: 100%;
    border-radius: 23px;
    overflow: hidden;
    margin-bottom: 25px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__item-btn {
    position: absolute;
    display: block;
    width: 60px;
    height: 60px;
    bottom: 160px;
    right: 0;
    background: none;
    padding: 0;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url(../img/cart-btn.svg);
    border: none;

    @media (max-width: 600px) {
      bottom: 100px;
    }
  }

  &__item-price {
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    text-transform: capitalize;
  }

  &__item-color {
    margin-bottom: 20px;
  }

  &__btn {
    margin: 0 130px 100px auto;
    display: flex;
    max-width: 260px;

    @media (max-width: 1200px) {
      margin: 0 50px 50px auto;
    }

    @media (max-width: 600px) {
      max-width: 130px;
    }
  }
}

.filter {
  min-width: 393px;
  margin-right: 15px;
  background: #FFFFFF;
  box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.05), 1px 1px 15px rgba(0, 0, 0, 0.05);
  border-radius: 25px;
  height: 950px;
  padding: 25px 5px;
  border: 1px solid #ccc;

  @media (max-width: 968px) {
    margin-bottom: 40px;
    margin-right: 0;
  }

  @media (max-width: 600px) {
    min-width: auto;
    max-width: 100%;
  }

  &__search-label {
    width: 100%;
    position: relative;
    margin-bottom: 25px;

    &:before {
      content: "";
      position: absolute;
      top: -5px;
      left: 10px;
      width: 40px;
      height: 40px;
      background-image: url(../img/search-icon.png);
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  &__search-input {
    border: none;
    width: 100%;
    background: #E8E8E8;
    border-radius: 25px;
    height: 50px;
    padding: 5px 10px;
    margin-bottom: 25px;
  }

  &__output {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    padding: 0 20px;
  }

  &__output-btn {
    color: $color-green;
  }

  &__list {
    height: 440px;
    // overflow: auto;
    padding: 0 20px;
    margin-bottom: 25px;
    padding: 15px 20px 10px;
    border-bottom: 1px solid $color-green;
  }
}

.tabs {
  @include tabs;
}

.tabs__nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 5px;
}

.tabs__nav-item {
  margin-bottom: 15px;
}

.tabs__nav-btn {
  border-radius: 15px;
  padding: 10px;
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  text-transform: uppercase;
}

.offer-lk {
  .tabs__nav-btn--active {
    background: #E0E0E0;
    color: $color-main;
  }


}

.check-box {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  padding-left: 20px;
}


.check-box:focus~p {
  color: $color-green;
}

.check-box:checked~p {
  color: $color-green;
}

.simplebar-track.simplebar-vertical {
  background: linear-gradient(270deg, #00CD66 0%, rgba(0, 205, 25, 0.15) 100%);
  width: 2px;
}



.simplebar-content-wrapper {
  padding-right: 15px;
}

.simplebar-scrollbar::before {
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  opacity: 1;
  width: 5px;
  background-color: $color-green;
  border-radius: 0;
}

.simplebar-track.simplebar-vertical .simplebar-scrollbar::before {
  top: 0;
  bottom: 0;
}

.simplebar-scrollbar.simplebar-visible::before {
  opacity: 1;
}