.mywallet {
  margin-bottom: 100px;
  background-color: $color-bg;

  @media (max-width: 600px) {
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: 75px;
    font-weight: 600;
    font-size: 60px;
    line-height: 70px;
    text-transform: capitalize;
    color: $color-main;
    text-align: left;

    @media (max-width: 600px) {
      font-size: 28px;
      line-height: 30px;
      margin-bottom: 30px;
    }
  }

  &__wrapper {
    background: #FFFFFF;
    box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.05), 1px 1px 15px rgba(0, 0, 0, 0.05);
    border-radius: 25px;
    padding: 50px 50px 50px 45px;
    position: relative;
    border: 1px solid $color-main;

    @media (max-width: 1280px) {
      padding: 50px 50px 140px 45px;
    }

    @media (max-width: 600px) {
      padding: 10px 10px 65px 10px;
      border-radius: 10px;
    }
  }

  &__inner {
    display: flex;

    @media (max-width: 968px) {
      flex-direction: column;
    }
  }

  &__cart {
    margin-right: 35px;
    width: 666px;
    height: 333px;
    border-radius: 25px;
    background-color: $color-green;
    position: relative;
    overflow: hidden;
    padding: 25px;
    color: #FFFFFF;
    font-weight: 600;
    font-size: 50px;
    line-height: 60px;
    text-transform: capitalize;

    @media (max-width: 1280px) {
      width: 400px;
      height: 200px;
      font-size: 30px;
      line-height: 34px;
    }

    @media (max-width: 968px) {
      margin-bottom: 30px;
    }

    @media (max-width: 600px) {
      width: 100%;
    }

    &:before {
      content: "";
      position: absolute;
      width: 400px;
      height: 400px;
      background-repeat: no-repeat;
      background-image: url(../img/cart-bg.svg);
      right: -80px;
      bottom: -180px;


    }
  }

  &__cart-name {

    margin-bottom: 25px;
  }

  &__item {
    padding-left: 115px;
    font-weight: 500;
    font-size: 32px;
    line-height: 32px;
    position: relative;

    @media (max-width: 1280px) {
      font-size: 28px;
      line-height: 30px;
    }

    @media (max-width: 600px) {
      padding: 0;
      font-size: 23px;
      line-height: 25px;

    }

    &:before {
      content: "";
      position: absolute;
      display: block;
      width: 100px;
      height: 23px;
      background-image: url(../img/arrow.svg);
      background-repeat: no-repeat;
      background-size: contain;
      top: 6px;
      left: 0;

      @media (max-width: 600px) {
        display: none;
      }
    }
  }

  &__item:not(:last-child) {
    margin-bottom: 35px;

    @media (max-width: 1280px) {
      margin-bottom: 20px;
    }
  }

  &__btn {
    position: absolute;
    bottom: 50px;
    right: 50px;

    @media (max-width: 600px) {
      bottom: 10px;
      right: 10px;
    }
  }
}