.offer {
  margin-bottom: 100px;
  background-color: $color-bg;

  @media (max-width: 600px) {
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: 75px;
    font-weight: 600;
    font-size: 60px;
    line-height: 70px;
    text-transform: capitalize;
    color: $color-main;
    text-align: left;

    @media (max-width: 600px) {
      font-size: 28px;
      line-height: 30px;
      margin-bottom: 30px;
    }
  }

  &__wrapper {
    background: #FFFFFF;
    box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.05), 1px 1px 15px rgba(0, 0, 0, 0.05);
    border-radius: 25px;
    padding: 50px 50px 200px 45px;
    position: relative;
    border: 1px solid $color-main;

    @media (max-width: 768px) {
      padding: 50px 50px 140px 45px;
    }

    @media (max-width: 600px) {
      padding: 10px 10px 70px 10px;
      border-radius: 10px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 100px;

    @media (max-width: 1200px) {
      gap: 30px;
    }

    @media (max-width: 968px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 768px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item {
    position: relative;
  }

  &__item-image {
    width: 100%;
    border-radius: 25px;
    overflow: hidden;
    margin-bottom: 25px;
    height: 470px;

    @media (max-width: 600px) {
      height: 300px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__item-content {
    max-width: 240px;
    font-weight: 500;
    font-size: 20px;

    @media (max-width: 600px) {
      font-size: 16px;
      line-height: 18px;
    }
  }

  &__item-color {
    margin-bottom: 15px;
  }

  &__item-btn {
    position: absolute;
    display: block;
    width: 60px;
    height: 60px;
    bottom: 150px;
    right: 0;
    background: none;
    padding: 0;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url(../img/offer-item-btn.svg);
    border: none;

    @media (max-width: 600px) {
      width: 40px;
      height: 40px;
      bottom: 90px;
    }
  }

  .offer__btn {
    position: absolute;
    bottom: 50px;
    right: 50px;

    @media (max-width: 600px) {
      bottom: 20px;
      right: 10px;
    }
  }
}

.offer__item.offer__item--empty {

  .offer__item-content {
    opacity: 0;
  }

  .offer__item-image {
    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.25);
    box-sizing: border-box;
    border-radius: 25px;
    position: relative;

    &::after {
      content: "нет товара";
      display: block;
      width: 100%;
      height: 100%;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      font-weight: 600;
      font-size: 32px;
      line-height: 42px;
      text-align: center;
      text-transform: uppercase;
      font-weight: 600;
      color: rgba(0, 0, 0, 0.5);
      padding-top: 50%;
    }

  }
}