.ancor {
  position: fixed;
  bottom: 50px;
  right: 25px;
  display: flex;
  flex-direction: column;

  &__btn {
    width: 50px;
    height: 50px;
    padding: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__btn:not(:last-child) {
    margin-bottom: 25px;
  }
}