.table {
  &__inner {
    background: #FFFFFF;
    box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.05), 1px 1px 15px rgba(0, 0, 0, 0.05);
    border-radius: 25px;
    padding: 50px 50px 100px 50px;

    @media (max-width: 968px) {
      padding: 15px;
      border-radius: 15px;
    }
  }

  &__wrapper {
    padding: 0;
  }

  &__title {
    font-weight: 600;
    font-size: 60px;
    line-height: 70px;
    color: $color-main;
    margin-bottom: 100px;

    @media (max-width: 968px) {
      font-size: 50px;
      line-height: 60px;
    }

    @media (max-width: 968px) {
      font-size: 30px;
      line-height: 40px;
      margin-bottom: 30px;
    }
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    margin-bottom: 50px;
    padding-bottom: 10px;
    border-bottom: 1px solid $color-green;
    gap: 10px;

    @media (max-width: 968px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__row-name {
    grid-column: span 4;

    @media (max-width: 968px) {
      margin: 0 auto;
    }
  }

  &__row-price {
    font-weight: 600;
    grid-column: span 4;
    margin-left: auto;

    @media (max-width: 968px) {
      margin: 0 auto;
    }
  }

  &__row-date {
    grid-column: span 4;
    margin-left: auto;

    @media (max-width: 968px) {
      margin: 0 auto;
    }
  }
}

.table-my {
  margin-bottom: 100px;

  @media (max-width: 600px) {
    margin-bottom: 50px;
  }
}