.header {
  width: 100%;
  background-color: $color-bg;
  padding: 10px 0;

  &__inner {
    min-height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    width: 35px;
    height: 35px;
  }

  &__login {
    width: 50px;
    height: 50px;
    background-image: url(../img/login-icon.png);
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.header-reg {
  .header__login {
    background-image: url(../img/main.svg);
    width: 177px;
    height: 32px;

  }

}