.footer {
  width: 100%;
  background-color: $color-bg;

  &__inner {
    min-height: 100px;
    border-top: 2px solid $color-green;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width:750px) {
      flex-direction: column;
    }
  }
}

.footer-noreg {
  .social__list {
    display: none;
  }
}