.message-main {
  &__wrapper {
    border-radius: 25px;
    border-right-color: $color-green;
    padding: 50px;
    background-color: $color-green;
    margin-bottom: 50px;
  }

  &__title {
    font-weight: 600;
    font-size: 60px;
    line-height: 70px;
    color: #FFFFFF;
    margin-bottom: 50px;

    @media (max-width:768px) {
      font-size: 30px;
      line-height: 40px;
    }
  }

  &__inner {
    display: flex;

    @media (max-width:1200px) {
      flex-direction: column;
    }
  }

  &__image {
    width: 50%;

    @media (max-width:1200px) {
      width: 100%;
      margin-bottom: 50px;
    }

    img {
      width: 100%;
      object-fit: contain;
    }
  }

  &__content {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media (max-width:1200px) {
      width: 100%;
    }
  }

  &__content-title {
    font-weight: 700;
    font-size: 100px;
    line-height: 110px;
    color: rgba(0, 0, 0, 0.05);

    @media (max-width:1800px) {
      font-size: 70px;
      line-height: 80px;
    }

    @media (max-width:768px) {
      font-size: 40px;
      line-height: 50px;
    }
  }

  &__btns {
    display: flex;
    flex-direction: column;
  }

  &__btn {
    font-weight: 600;
    font-size: 22px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    color: $color-green;
    background-color: #fff;
    padding: 20px 40px;
    border-radius: 25px;
    margin-bottom: 30px;
    margin-left: auto;

    @media (max-width:768px) {
      font-size: 16px;
      line-height: 18px;
      padding: 10px 20px;
    }
  }
}

.chat {
  &__wrapper {
    border-radius: 25px;
    background-color: $color-green;
    padding: 50px;
    margin-bottom: 50px;

    @media (max-width: 768px) {
      padding: 10px;
      border-radius: 10px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 60px;
    line-height: 70px;
    color: #FFFFFF;
    margin-bottom: 50px;
    text-align: left;

    @media (max-width: 1200px) {
      font-size: 40px;
      line-height: 50px;
    }

    @media (max-width: 768px) {
      font-size: 28px;
      line-height: 30px;
    }

    @media (max-width: 500px) {
      font-size: 18px;
      line-height: 20px;
    }
  }

  &__inner {
    background: #FFFFFF;
    border: 2px solid $color-green;
    box-sizing: border-box;
    border-radius: 25px;
    padding: 50px;
    min-height: 600px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    @media (max-width: 768px) {
      padding: 10px;
      border-radius: 10px;
    }
  }

  &__list {
    margin-bottom: 25px;
    margin-top: auto;
    width: 100%;
  }

  &__item {

    font-weight: 400;
    font-size: 22px;
    line-height: 140%;
    max-width: 450px;
    margin-bottom: 25px;

    @media (max-width: 500px) {
      font-size: 16px;
    }
  }

  &__item-ask {
    color: $color-main;
    background: #E8E8E8;
    margin-right: auto;
    border-radius: 10px;
    padding: 10px 20px;
    display: inline-flex;
  }

  &__item-answ {
    color: #fff;
    background: #007AFF;
    margin-left: auto;
    border-radius: 10px;
    padding: 10px 20px;
    display: inline-flex;
  }

  &__item-right {
    display: block;
    margin-left: auto;
  }

  &__form {
    display: flex;
    width: 100%;
    position: relative;

    @media (max-width: 500px) {
      flex-direction: column;
    }
  }

  &__form-label {
    width: 100%;
    position: relative;
    margin-right: 25px;
  }

  .chat__form-label {
    margin-bottom: 0;
  }

  &__form-input {
    outline: 0;
    opacity: 0;
    pointer-events: none;
    user-select: none;
    width: 0;
  }

  &__form-file {
    position: absolute;
    left: 50px;
    z-index: 1;

    &::before {
      content: "";
      position: absolute;
      display: block;
      top: 13px;
      right: 0;
      width: 40px;
      height: 40px;
      background-image: url(../img/file.png);
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  &__form-textarea {
    max-width: 100%;
    padding-left: 100px;
  }

  &__form-btn {
    min-width: 70px;
    height: 70px;
    border-radius: 50%;

    @media (max-width: 500px) {
      max-width: 50px;

    }

    img {
      width: 100%;
      object-fit: contain;
    }
  }
}