html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  color: $color-main;
  font-size: 22px;
  line-height: 32px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  margin: 0;
  padding: 0;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  background-color: $color-bg;
  position: relative;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 100px;
  max-width: 1920px;

  @media (max-width: 1250px) {
    padding: 0 50px;
  }

  @media (max-width: 500px) {
    padding: 0 10px;
  }
}

.container-wide {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1920px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  height: 70px;
  background: $color-green;
  border-radius: 25px;
  color: #fff;
  font-weight: 600;
  font-size: 22px;
  line-height: 32px;
  text-align: center;
  text-transform: uppercase;
  padding: 20px 50px;
  transition: all 0.3s ease-in-out;
  border: 1px solid $color-green;

  &:hover {
    color: $color-green;
    background-color: #fff;
  }

  @media (max-width: 500px) {
    font-size: 14px;
    line-height: 16px;
    padding: 10px 20px;
    height: 40px;
  }
}

.title {
  margin: 0;
  font-weight: 700;
  font-size: 150px;
  line-height: 130px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: $color-green;

  @media (max-width: 1750px) {
    font-size: 100px;
    line-height: 90px;
  }

  @media (max-width: 1200px) {
    font-size: 50px;
    line-height: 40px;
  }

  @media (max-width: 650px) {
    font-size: 26px;
    line-height: 24px;
  }
}