.history {
  &__title {
    font-weight: 600;
    font-size: 60px;
    line-height: 70px;
    color: $color-main;
    text-align: left;
    margin-bottom: 50px;

    @media (max-width:968px) {
      font-size: 40px;
      line-height: 50px;
    }

    @media (max-width:650px) {
      font-size: 30px;
      line-height: 40px;
      margin-bottom: 30px;
    }
  }

  &__content {
    padding: 50px 50px 70px 50px;

    @media (max-width:650px) {
      padding: 10px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 100px;

    @media (max-width:1440px) {
      grid-template-columns: repeat(2, 1fr);
      gap: 50px;
    }

    @media (max-width:968px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 30px;
    }
  }

  &__image {
    height: 470px;

    @media (max-width:650px) {
      height: 300px;
    }
  }

  &__search {
    position: relative;
    margin-left: 50px;
    margin-bottom: 0;

    @media (max-width:1440px) {
      margin-left: 0;
      margin-bottom: 50px;
    }

    &:before {
      content: "";
      position: absolute;
      top: 5px;
      left: 10px;
      width: 50px;
      height: 50px;
      background-image: url(../img/search-icon.png);
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;

    @media (max-width:1440px) {
      flex-direction: column;
    }
  }

  &__search-input {
    min-width: 300px;
    width: 485px;
    height: 60px;
    background: #E8E8E8;
    border-radius: 25px;
    padding: 10px 10px 10px 80px;

    @media (max-width:650px) {
      width: 100%;
    }

    &::placeholder {
      color: #95A5A6;
    }
  }

  &__star {
    display: flex;
    margin-bottom: 15px;
  }

  &__star-item:not(:last-child) {
    margin-right: 15px;

  }

  &__star-item {
    width: 50px;
    height: 50px;

    @media (max-width:650px) {
      width: 40px;
      height: 40px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

  }
}

.offer-lk__item-date {
  color: rgba(0, 0, 0, 0.5);
}