.lk-modal {
  .graph-modal__container {
    max-width: 792px;
    height: 992px;
    background: #FFFFFF;
    border-radius: 25px;
    padding: 25px 25px 40px 25px;

    @media (max-width:968px) {
      max-width: 100%;
    }

    @media (max-width:500px) {
      height: auto;
      padding: 10px 10px 50px 10px;
      border-radius: 10px;
    }
  }

  .graph-modal-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .lk-modal__send {
    position: absolute;
    bottom: 50px;
    right: 25px;
    width: 70px;
    height: 70px;

    @media (max-width:500px) {
      width: 40px;
      height: 40px;
      bottom: 10px;
      right: 10px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  .graph-modal__close {
    width: 60px;
    height: 60px;
    top: 25px;
    right: 25px;

    @media (max-width:500px) {
      width: 40px;
      height: 40px;
      top: 10px;
      right: 10px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 50px;
    line-height: 60px;

    @media (max-width:500px) {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 40px;
    }
  }

  .call-modal__phone {
    font-weight: 600;
    font-size: 50px;
    line-height: 60px;

    @media (max-width:500px) {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 40px;
    }
  }

  .call-modal__logo {
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    color: $color-green;

    span {
      color: $color-main;
    }
  }

  .call-modal__link {
    font-weight: 600;
    font-size: 22px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    background-color: $color-green;
    width: 258px;
    height: 70px;
    border-radius: 25px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
  }

  &__textarea {
    width: 100%;
    height: 70px;
    background: #E8E8E8;
    border-radius: 25px;
    padding: 18px;
    resize: none;

    @media (max-width:500px) {
      font-size: 16px;
      border-radius: 10px;
      padding: 10px;
    }

    &::placeholder {
      font-weight: 400;
      font-size: 22px;
      line-height: 32px;
      color: #95A5A6;

      @media (max-width:500px) {
        font-size: 16px;
      }
    }
  }

  .form__label {
    margin-bottom: 0;
    margin-right: 25px;

    @media (max-width:500px) {
      margin-right: 0;
    }
  }

  .mymessage__cart-vol {
    margin-bottom: 75px;
    margin-top: auto;
    max-width: 441px;
    height: 76px;
    font-weight: 400;
    font-size: 22px;
    line-height: 140%;
    color: #FFFFFF;

    @media (max-width:500px) {
      font-size: 16px;
    }
  }
}

.lk-modal.graph-modal {
  background-color: rgba(196, 196, 196, 0.5);
}

.lk-modal .graph-modal__container--call {
  height: 482px;
}