.mainscreen {
  width: 100%;
  background-color: $color-bg;

  &__container {
    min-height: 100%;
  }

  &__section {
    width: 100%;
    position: relative;
  }


  &__title {
    margin-bottom: 100px;

    @media (max-width: 500px) {
      margin-bottom: 30px;
    }
  }

  &__title--offer {
    margin-bottom: 180px;

    @media (max-width: 1200px) {
      margin-bottom: 50px;
    }
  }

  &__card {
    background: #FFFFFF;
    box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.05), 1px 1px 15px rgba(0, 0, 0, 0.05);
    border-radius: 25px;
    padding: 50px 50px 50px 40px;
    margin-bottom: 100px;
    position: relative;

    @media (max-width: 500px) {
      padding: 10px;
      border-radius: 10px;
    }
  }

  &__text {
    max-width: 560px;
    margin-bottom: 200px;

    @media (max-width: 1200px) {
      margin-bottom: 50px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    @media (max-width: 968px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
}

.mainscreen-offer {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  @media (max-width: 1400px) {
    flex-wrap: wrap;
  }

  &__item {
    max-width: 540px;

    @media (max-width: 1250px) {
      margin-bottom: 50px;
      max-width: 100%;
    }

    @media (max-width: 500px) {
      margin-bottom: 20px;
      max-width: 100%;
    }
  }

  &__item:not(:last-child) {
    margin-bottom: 100px;

    @media (max-width: 1250px) {
      margin-bottom: 50px;
      max-width: 100%;
    }

    @media (max-width: 500px) {
      margin-bottom: 20px;
      max-width: 100%;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 60px;
    line-height: 70px;
    text-transform: uppercase;
    margin-bottom: 10px;
    border-bottom: 2px solid $color-green;
    position: relative;

    @media (max-width: 1400px) {
      font-size: 40px;
    }

    @media (max-width: 500px) {
      font-size: 28px;
    }

    &:after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 55px;
      height: 55px;
      background-size: cover;
      background-repeat: no-repeat;

      @media (max-width: 500px) {
        width: 30px;
        height: 30px;
        top: 18px;
      }
    }
  }

  &__title--log {
    &:after {
      background-image: url(../img/logistic-icon.png);
    }
  }

  &__title--choice {
    &:after {
      background-image: url(../img/choice-icon.png);
    }
  }

  &__title--proc {
    &:after {
      background-image: url(../img/proc-icon.png);
    }
  }

  &__title--task {
    &:after {
      background-image: url(../img/task-icon.png);
    }
  }

  .btn__reg {
    @media (max-width: 1400px) {
      margin-top: 20px;
    }
  }

  &__col {
    @media (max-width: 1250px) {
      width: 100%;
    }
  }

  &__text {
    @media (max-width: 500px) {
      font-size: 14px;
      line-height: 16px;
    }

  }

}

.main__title {
  padding-bottom: 50px;
  border-bottom: 2px solid $color-green;
  margin-bottom: 50px;

  @media (max-width: 500px) {
    padding-bottom: 20px;
    margin-bottom: 20px;
  }

  span {
    color: $color-main;
  }
}

.mainscreen__text {
  @media (max-width: 500px) {
    font-size: 14px;
    line-height: 16px;
  }
}

.main__subtitle {
  padding-bottom: 50px;
  border-bottom: 2px solid $color-green;
  color: $color-main;
  margin-bottom: 25px;

  @media (max-width: 500px) {
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
}

.social__list {
  display: flex;
  align-items: center;

  @media (max-width: 968px) {
    margin-bottom: 20px;
  }
}

.social__item {
  width: 50px;
  height: 50px;
  border: 1px solid #000;
  border-radius: 50%;
  padding: 7px;
  transition: all 0.3s ease-in-out;

  &:hover {
    border: 1px solid $color-green;
    background-color: #fff;
  }

  @media (max-width: 500px) {
    width: 30px;
    height: 30px;
  }
}

.social__link {
  display: block;
  width: 100%;
  height: 100%;
  fill: $color-green;
  background-size: cover;
  background-repeat: no-repeat;
}

.social__item:not(:last-child) {
  margin-right: 25px;

  @media (max-width: 500px) {
    margin-right: 10px;
  }
}

.mainscreen-goods {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 100px;

  @media (max-width: 1250px) {
    gap: 30px;
  }

  @media (max-width: 750px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 500px) {
    grid-template-columns: repeat(1, 1fr);
  }

  &__image {
    // border: 1px solid rgba(0, 0, 0, 0.25);
    border-radius: 25px;
    width: 100%;
    height: 470px;
    margin-bottom: 25px;
    overflow: hidden;

    @media (max-width: 1250px) {
      height: 300px;
    }

    @media (max-width: 968px) {
      height: 250px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__price {
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    text-align: right;
    text-transform: capitalize;

    @media (max-width: 1450px) {
      font-size: 24px;
      line-height: 28px;
    }

    @media (max-width: 968px) {
      font-size: 18px;
      line-height: 20px;
    }
  }

  &__text {
    @media (max-width: 968px) {
      font-size: 18px;
      line-height: 20px;
    }
  }
}

.mainscreen-about {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
  position: relative;

  @media (max-width: 1280px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 600px) {
    grid-template-columns: repeat(1, 1fr);
  }

  &:before {
    content: "";
    position: absolute;
    top: -260px;
    left: 0;
    background-image: url(../img/main-about-bg.svg);
    background-size: contain;
    background-repeat: no-repeat;
    width: 100%;
    height: 380px;

    @media (max-width: 1780px) {
      display: none;
    }
  }

  &__card {
    border: 2px solid $color-green;
    box-sizing: border-box;
    border-radius: 25px;
    min-height: 309px;
    padding: 25px;
    margin-top: auto;

    @media (max-width: 1400px) {
      padding: 15px;
      min-height: 100%;
    }
  }

  &__card:nth-child(even) {
    margin-bottom: 150px;

    @media (max-width: 1400px) {
      margin-bottom: 80px;
    }

    @media (max-width: 1280px) {
      margin-bottom: 0;
    }
  }

  &__card-title {
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    text-transform: uppercase;
    margin-bottom: 25px;

    @media (max-width: 1400px) {
      font-size: 24px;
      line-height: 26px;
    }

    @media (max-width: 1400px) {
      font-size: 22px;
      line-height: 24px;
    }
  }

  &__card-text {
    font-size: 24px;
    line-height: 26px;

    @media (max-width: 1400px) {
      font-size: 18px;
      line-height: 20px;
    }

    @media (max-width: 500px) {
      font-size: 14px;
      line-height: 16px;
    }
  }
}

.mainscreen__title--about {
  margin-bottom: 25px;
}

.mainscreen__title--reg {
  margin-bottom: 50px;

}

.mainscreen__card--reg {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: bottom left;
    background-image: url(../img/mainreg.svg);
    width: 75%;
    height: 70%;
    bottom: 0;
    left: 50px;

    @media (max-width: 1000px) {
      width: 100%;
      left: 0;
      height: 85%;
    }
  }

}

.mainscreen-reg {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  position: relative;

  &__image {
    height: 650px;
    width: 100%;
    margin-bottom: -50px;

    @media (max-width: 1000px) {
      height: 480px;
    }

    @media (max-width: 768px) {
      height: 360px;
    }


    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__buttons {
    margin-bottom: 25px;
    text-align: right;
  }

}

.social__list--vertical {
  position: absolute;
  top: -100px;
  right: 0;
  flex-direction: column;

  @media (max-width: 1000px) {
    top: 30%;
    flex-direction: row;

  }

  @media (max-width: 768px) {
    top: 0;
  }


  .social__item {
    margin-right: 0;
    margin-bottom: 25px;
    border: 1px solid #000;
    border-radius: 50%;
    padding: 7px;
    transition: all 0.3s ease-in-out;

    &:hover {
      border: 1px solid $color-green;
      background-color: #5de3ec;
    }

    @media (max-width: 1000px) {
      margin-right: 25px;
      margin-bottom: 0;

    }
  }
}

.btn__mainreg {
  background-color: $color-main;
  margin-bottom: 25px;
  width: 395px;
  display: block;

  @media (max-width: 500px) {
    width: 250px;
  }
}

.mainscreen__card-first {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 0;
}


.mainscreen__card--about {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.mainscreen__subtitle--about {
  margin-bottom: 90px;

  @media (max-width: 1280px) {
    margin-bottom: 20px;
  }

  @media (max-width: 500px) {
    font-size: 18px;
    line-height: 20px;
  }
}

.mainscreen__counter {
  position: absolute;
  bottom: 0;
  right: -75px;
  font-weight: 500;
  font-size: 32px;
  line-height: 42px;

  span {
    font-size: 22px;
    opacity: 0.5;
  }

  @media (max-width: 1260px) {
    display: none;
  }
}