.single {
  &__title {
    font-weight: 600;
    font-size: 60px;
    line-height: 70px;
    color: $color-main;
    max-width: 445px;
    margin-bottom: 75px;

    @media (max-width: 600px) {
      font-size: 28px;
      line-height: 30px;
    }
  }

  &__content {
    display: flex;
    padding: 50px 90px 100px 50px;

    @media (max-width: 1450px) {
      padding: 30px;
    }

    @media (max-width: 1200px) {
      flex-direction: column;
    }
  }

  &__filter {
    display: flex;
    justify-content: space-between;

    @media (max-width: 600px) {
      flex-direction: column;
    }
  }

  &__filter-title {
    width: 60%;
    font-weight: 400;
    font-size: 32px;
    line-height: 42px;
    max-width: 245px;

    @media (max-width: 600px) {
      font-size: 28px;
      line-height: 30px;
      width: 100%;
    }
  }

  &__filter-list {
    display: flex;
    width: 40%;
    font-size: 22px;
    line-height: 32px;
    margin-bottom: 35px;
    max-width: 225px;
    flex-wrap: wrap;

    @media (max-width: 600px) {
      font-size: 24px;
      line-height: 26px;
      width: 100%;
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    width: 50%;

    @media (max-width: 1200px) {
      width: 100%;
    }
  }

  &__image {
    width: 50%;
    border-radius: 25px;
    overflow: hidden;
    margin-right: 150px;

    @media (max-width: 1450px) {
      margin-right: 50px;
    }

    @media (max-width: 1200px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 50px;
    }

    img {
      width: 100%;
      object-fit: cover;
    }
  }

  &__filter-item {
    p {
      border-radius: 15px;
      padding: 10px;
      background-color: transparent;
    }
  }

  &__filter-item:not(:last-child) {
    margin-right: 5px;
  }

  .check-box:checked~p {
    color: #fff;
    background-color: $color-green;

  }

  &__place,
  &__color {
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
    font-size: 32px;
    line-height: 42px;

    @media (max-width: 600px) {
      font-size: 24px;
      line-height: 26px;
      flex-direction: column;
    }
  }

  &__place-name {
    text-transform: uppercase;
    color: $color-green;
  }

  &__descr {
    margin-bottom: 50px;
  }

  &__descr-title {
    font-size: 32px;
    line-height: 42px;
    margin-bottom: 20px;

    @media (max-width: 600px) {
      font-size: 24px;
      line-height: 26px;
    }
  }

  &__descr-text {
    max-width: 600px;
  }

  &__price {
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    text-transform: capitalize;
    margin-bottom: 50px;
  }

  &__btn {
    width: 258px;
    height: 70px;
    background: $color-green;
    border-radius: 25px;
    color: #fff;
  }
}

.change {
  .single__title {
    background-color: #E0E0E0;
    border-radius: 15px;
    padding: 5px;

    @media (max-width: 1750px) {
      margin-top: 80px;
    }
  }

  .single__descr-text {
    background-color: #E0E0E0;
    border-radius: 15px;
    padding: 5px;
  }

  .single__image {
    position: relative;

    img {
      filter: brightness(0.3);
    }

    &:before {
      content: "";
      position: absolute;
      display: block;
      width: 100px;
      height: 100px;
      top: 350px;
      left: 50%;
      transform: translate(-50%, -50%);
      background-image: url(../img/photo-white.png);
      background-repeat: no-repeat;
      background-size: contain;
      z-index: 1;
    }

  }

  .single__right {
    position: relative;
  }

  .single__right-buttons {
    position: absolute;
    top: 0;
    right: 0;


  }

  &__submit {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: $color-green;
    margin-right: 25px;
    padding: 10px;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__cancel {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: $color-green;
    padding: 10px;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}

.add {
  .single__image {
    background-color: #363636;
    max-height: 800px;

    @media (max-width: 1750px) {
      max-height: 600px;
      height: 600px;
    }

    @media (max-width: 500px) {
      height: 300px;
    }
  }

  .single__image:before {
    @media (max-width: 500px) {
      top: 150px;
    }
  }

  &__loadimage {
    width: 100%;
    height: 100%;
    outline: 0;
    opacity: 0;
    // pointer-events: none;
    // user-select: none;
  }
}