.regform {
  &__inner {
    height: 100%;
    padding: 50px;
    background-color: #fff;
    border-radius: 40px;
    margin: 50px 0 100px;
    box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.05), 1px 1px 15px rgba(0, 0, 0, 0.05);
    border: 1px solid #ccc;

    @media (max-width: 500px) {
      padding: 10px;
      border-radius: 10px;
    }
  }



  &__title {
    color: $color-green;
    margin-bottom: 100px;
    font-size: 100px;
    line-height: 110px;

    @media (max-width: 1280px) {
      margin-bottom: 50px;
      font-size: 70px;
      line-height: 80px;
    }

    @media (max-width: 650px) {
      font-size: 26px;
      line-height: 24px;
    }

  }

  &__form {
    display: flex;
    justify-content: space-between;

    @media (max-width: 1280px) {
      flex-direction: column;
    }
  }

  &-left {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-right: 20px;

    @media (max-width: 1280px) {
      margin-bottom: 30px;
      width: 100%;
      margin-right: 0;
    }
  }

  &-right {
    display: flex;
    flex-direction: column;
    width: 50%;
    justify-content: space-between;
    align-items: flex-end;

    @media (max-width: 1280px) {
      width: 100%;
      align-items: flex-start;
    }
  }
}

.form {
  &__input {
    background: transparent;
    border: none;
    // border-bottom: 2px solid $color-main;
    width: 100%;
    max-width: 650px;

    &::placeholder {
      font-weight: 400;
      font-size: 22px;
      line-height: 32px;
      letter-spacing: 0.03em;
      color: $color-main;

      @media (max-width: 500px) {
        font-size: 18px;
        line-height: 20px;
      }
    }
  }

  &__label:not(:last-child) {
    margin-bottom: 50px;

    @media (max-width: 500px) {
      margin-bottom: 30px;
    }
  }

  &__input-file {
    outline: 0;
    opacity: 0;
    pointer-events: none;
    user-select: none;
  }
}

.form__label-file {
  width: 530px;
  height: 330px;
  background-color: #FFFFFF;
  border-radius: 25px;
  position: relative;
  box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.05), 1px 1px 15px rgba(0, 0, 0, 0.05);
  border: 1px solid $color-main;

  @media (max-width: 1450px) {
    width: 100%;
  }

  @media (max-width: 1280px) {
    width: 100%;
    height: 150px;
  }

  &:before {
    content: "Загрузить фото";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 260px;
    height: 75px;
    border-radius: 24px;
    color: #fff;
    background-color: $color-green;
    padding: 20px 40px;
    // background-image: url(../img/photo.png);
    // background-repeat: no-repeat;
    // background-size: contain;


  }
}

textarea {
  resize: none;
  height: 50px;
  ;
}

.regform__btn {
  background-color: $color-green;
  ;
  color: #fff;
}